
import React, { useState, useEffect } from 'react';
import { MenuIcon } from './icons/MenuIcon';
import { CloseIcon } from './icons/CloseIcon';
import { ChatIcon } from './icons/ChatIcon'; // Import ChatIcon

const NavLinks = () => (
    <>
        <a href="#home" className="text-gray-700 dark:text-gray-200 hover:text-green-600 dark:hover:text-green-400 transition-colors">Beranda</a>
        <a href="#profil" className="text-gray-700 dark:text-gray-200 hover:text-green-600 dark:hover:text-green-400 transition-colors">Profil</a>
        <a href="#berita" className="text-gray-700 dark:text-gray-200 hover:text-green-600 dark:hover:text-green-400 transition-colors">Berita</a>
        <a href="#kontak" className="text-gray-700 dark:text-gray-200 hover:text-green-600 dark:hover:text-green-400 transition-colors">Kontak</a>
    </>
);

interface HeaderProps {
    onLoginClick: () => void;
    onChatClick: () => void; // New prop for chat
    isLoggedIn: boolean;
    onLogout: () => void;
}

const Header: React.FC<HeaderProps> = ({ onLoginClick, onChatClick, isLoggedIn, onLogout }) => {
    const [isMenuOpen, setIsMenuOpen] = useState(false);
    const [isScrolled, setIsScrolled] = useState(false);

    useEffect(() => {
        const handleScroll = () => {
            setIsScrolled(window.scrollY > 10);
        };
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, []);
    
    useEffect(() => {
        const handleResize = () => {
            if (window.innerWidth >= 768) {
                setIsMenuOpen(false);
            }
        };
        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, []);

    // Demo function untuk testing portal orang tua
    const switchToParentDemo = () => {
        if (typeof window !== 'undefined' && window.localStorage) {
            const parentUser = {
                id: 4,
                email: 'parent@ma-malnukananga.sch.id',
                name: 'Bapak Ahmad Rahman',
                role: 'parent',
                created_at: new Date().toISOString(),
                updated_at: new Date().toISOString(),
                is_active: true
            };
            localStorage.setItem('malnu_auth_current_user', JSON.stringify(parentUser));
            window.location.reload();
        }
    };

    const headerClasses = `
        fixed top-0 left-0 right-0 z-40 transition-all duration-300
        ${isScrolled ? 'mt-0 rounded-none shadow-md' : 'mt-4 mx-2 sm:mx-4 rounded-full'}
    `;

    const navContainerClasses = `
        w-full max-w-7xl mx-auto px-4
        ${isScrolled ? '' : 'bg-white/70 dark:bg-gray-800/70 backdrop-blur-lg rounded-full ring-1 ring-gray-900/5 dark:ring-white/10 shadow-lg shadow-black/5'}
    `;

    const innerNavClasses = `
        flex items-center justify-between h-16
        ${isScrolled ? 'bg-white/95 dark:bg-gray-800/95 backdrop-blur-lg' : ''}
    `;

    return (
        <header className={headerClasses}>
            <div className={navContainerClasses}>
                 <div className={`${innerNavClasses} ${isScrolled ? 'max-w-7xl mx-auto px-4' : ''}`}>
                    <div className="flex items-center space-x-3">
                        <div className="flex-shrink-0 w-8 h-8 bg-green-600 rounded-full flex items-center justify-center text-white font-bold text-lg">
                            M
                        </div>
                        <div>
                            <span className="font-bold text-lg text-gray-800 dark:text-white leading-tight">Malnu Kananga</span>
                            <span className="block text-xs text-gray-500 dark:text-gray-400 leading-tight tracking-wider">NPSN: 69881502</span>
                        </div>
                    </div>

                    {/* Desktop Nav */}
                    <nav className="hidden md:flex items-center space-x-8 font-medium">
                        <NavLinks />
                    </nav>

                    <div className="flex items-center space-x-2">
                        {isLoggedIn ? (
                             <div className="hidden sm:flex items-center space-x-2">
                                <a href="#" className="bg-green-600 text-white px-5 py-2 rounded-full font-semibold text-sm hover:bg-green-700 transition-colors">
                                    Portal
                                </a>
                                <button onClick={onLogout} className="bg-gray-200 dark:bg-gray-700 text-gray-800 dark:text-white px-5 py-2 rounded-full font-semibold text-sm hover:bg-gray-300 dark:hover:bg-gray-600 transition-colors">
                                    Logout
                                </button>
                            </div>
                        ) : (
                            <div className="hidden sm:flex items-center space-x-2">
                                <button onClick={onChatClick} className="flex items-center gap-2 bg-green-100 text-green-700 dark:bg-green-900/50 dark:text-green-300 px-4 py-2 rounded-full font-semibold text-sm hover:bg-green-200 dark:hover:bg-green-800 transition-colors">
                                    <ChatIcon />
                                    <span>Tanya AI</span>
                                </button>
                                <button onClick={switchToParentDemo} className="flex items-center gap-2 bg-blue-100 text-blue-700 dark:bg-blue-900/50 dark:text-blue-300 px-4 py-2 rounded-full font-semibold text-sm hover:bg-blue-200 dark:hover:bg-blue-800 transition-colors">
                                    <span> Demo Orang Tua</span>
                                </button>
                                <button onClick={onLoginClick} className="bg-green-600 text-white px-5 py-2 rounded-full font-semibold text-sm hover:bg-green-700 transition-colors">
                                    Login
                                </button>
                            </div>
                        )}
                        {/* Mobile Menu Button */}
                        <button onClick={() => setIsMenuOpen(!isMenuOpen)} className="md:hidden p-2 rounded-md text-gray-700 dark:text-gray-200 hover:bg-gray-100 dark:hover:bg-gray-700" aria-label="Buka menu">
                            {isMenuOpen ? <CloseIcon /> : <MenuIcon />}
                        </button>
                    </div>
                </div>
            </div>

            {/* Mobile Menu */}
            {isMenuOpen && (
                <div className="md:hidden bg-white dark:bg-gray-800 shadow-lg mx-2 sm:mx-4 rounded-2xl mt-2 p-4">
                    <nav className="flex flex-col space-y-4 font-medium text-center">
                        <NavLinks />
                         <div className="pt-4 border-t border-gray-200 dark:border-gray-700 flex flex-col space-y-3">
                            {isLoggedIn ? (
                                <>
                                    <a href="#" className="bg-green-600 text-white px-5 py-2 rounded-full font-semibold text-sm hover:bg-green-700 transition-colors">
                                        Portal
                                    </a>
                                    <button onClick={() => { onLogout(); setIsMenuOpen(false); }} className="bg-gray-200 dark:bg-gray-700 text-gray-800 dark:text-white px-5 py-2 rounded-full font-semibold text-sm">
                                        Logout
                                    </button>
                                </>
                            ) : (
                                <>
                                     <button onClick={() => { onChatClick(); setIsMenuOpen(false); }} className="bg-green-100 text-green-700 dark:bg-green-900/50 dark:text-green-300 w-full text-center px-5 py-2 rounded-full font-semibold text-sm">
                                        Tanya AI
                                    </button>
                                    <button onClick={() => { onLoginClick(); setIsMenuOpen(false); }} className="bg-green-600 text-white w-full text-center px-5 py-2 rounded-full font-semibold text-sm hover:bg-green-700 transition-colors">
                                        Login
                                    </button>
                                </>
                            )}
                         </div>
                    </nav>
                </div>
            )}
        </header>
    );
};

export default Header;